.dd-item {
  button[data-action='collapse'], button[data-action='expand'] {
    padding: 6px 10px;
    display: inline-block;
    position: absolute;
    margin: 0;
    left: 0;
    top: 0;
    bottom: 0;
    cursor: pointer;
    width: 30px;
    height: 30px;
    white-space: nowrap;
    overflow: hidden;
    color: transparent;
    outline: none;
    border: none;
    &:hover {
      &:before {
        color: #676a6c;
      }
    }
    &:before {
      color: #c1c1c1;
      font-size: 16px;
      display: inline-block !important;
      position: absolute;
      top: 9px;
      left: 4px;
      width: auto;
      text-indent: 100%;
    }
    & + .dd-handle {
      left: 30px;
      & + .dd3-content {
        padding-left: 60px;
      }
    }
  }
  .dd-handle {
    color: inherit;
    border: 1px dashed #e7eaec;
    border-right: none;
    background: #f3f3f4;
    padding: 6px 10px;
    display: inline-block !important;
    border-radius: 3px;
    &.dd3-handle {
      position: absolute;
      margin: 0;
      left: 0;
      top: 0;
      cursor: pointer;
      width: 30px;
      white-space: nowrap;
      border: none;
      overflow: hidden;
      box-shadow: none;
      &:hover {
        i {
          color: #676a6c;
        }
      }
      i {
        color: #c1c1c1;
      }
    }
  }
  .dd3-content, .dd-content {
    display: block !important;
    margin: 5px 0;
    text-decoration: none;
    box-sizing: border-box;
    color: #676a6c;
    border: 1px dashed #e7eaec;
    background: #f3f3f4;
    padding: 6px 10px 6px 30px;
    border-radius: 3px;
    a.title {
      color: #676a6c;
      &:hover {
        color: darken(#676a6c, 20%);
      }
    }
  }
}

