  display: none;
}
.dd-item button[data-action='collapse'],
.dd-item button[data-action='expand'] {
  padding: 6px 10px;
  display: inline-block;
  position: absolute;
  margin: 0;
  left: 0;
  top: 0;
  bottom: 0;
  cursor: pointer;
  width: 30px;
  height: 30px;
  white-space: nowrap;
  overflow: hidden;
  color: transparent;
  outline: none;
  border: none;
}
.dd-item button[data-action='collapse']:hover:before,
.dd-item button[data-action='expand']:hover:before {
  color: #676a6c;
}
.dd-item button[data-action='collapse']:before,
.dd-item button[data-action='expand']:before {
  color: #c1c1c1;
  font-size: 16px;
  display: inline-block !important;
  position: absolute;
  top: 9px;
  left: 4px;
  width: auto;
  text-indent: 100%;
}
.dd-item button[data-action='collapse'] + .dd-handle,
.dd-item button[data-action='expand'] + .dd-handle {
  left: 30px;
}
.dd-item button[data-action='collapse'] + .dd-handle + .dd3-content,
.dd-item button[data-action='expand'] + .dd-handle + .dd3-content {
  padding-left: 60px;
}
.dd-item .dd-handle {
  color: inherit;
  border: 1px dashed #e7eaec;
  border-right: none;
  background: #f3f3f4;
  padding: 6px 10px;
  display: inline-block !important;
  border-radius: 3px;
}
.dd-item .dd-handle.dd3-handle {
  position: absolute;
  margin: 0;
  left: 0;
  top: 0;
  cursor: pointer;
  width: 30px;
  white-space: nowrap;
  border: none;
  overflow: hidden;
  box-shadow: none;
}
.dd-item .dd-handle.dd3-handle:hover i {
  color: #676a6c;
}
.dd-item .dd-handle.dd3-handle i {
  color: #c1c1c1;
}
.dd-item .dd3-content,
.dd-item .dd-content {
  display: block !important;
  margin: 5px 0;
  text-decoration: none;
  box-sizing: border-box;
  color: #676a6c;
  border: 1px dashed #e7eaec;
  background: #f3f3f4;
  padding: 6px 10px 6px 30px;
  border-radius: 3px;
}
.dd-item .dd3-content a.title,
.dd-item .dd-content a.title {
  color: #676a6c;
}
.dd-item .dd3-content a.title:hover,
.dd-item .dd-content a.title:hover {
  color: #353738;
}
